import React from 'react'
import { Link, } from 'react-router-dom'
import { Typography, Card, CardContent, CardMedia, } from '@mui/material'
import { CheckCircle, } from '@mui/icons-material'
import he from 'he'

import { demoThumbnailUrl, demoVideoUrl, demoVideoTitle, demoChannelUrl, demoChannelTitle, } from '../utils/constants'

const VideoCard = ({ video: { id: { videoId }, snippet, }, }) => {
  return (
    <Card sx={{ width: { xs: '100%', sm: '358px', md: '320px', }, boxShadow: 'none', borderRadius: 0, }}>
      <Link to={videoId ? `/video/${videoId}` : demoVideoUrl}>
        <CardMedia
          image={he.decode(snippet?.thumbnails?.high?.url)}
          alt={he.decode(snippet?.title)}
          sx={{ width: { xs: '100%', sm: '358px', md: '320px', }, height: 180, }}
        />
      </Link>
      <CardContent sx={{ backgroundColor: '#1e1e1e', height: '106px', }} >
        <Link to={videoId ? `/video/${videoId}` : demoVideoUrl}>
          <Typography variant='subtitle1' fontWeight='bold' color='#fff'>
            {(snippet?.title && he.decode(snippet?.title.slice(0, 60))) || demoVideoTitle.slice(0, 60)}
          </Typography>
        </Link>
        <Link to={videoId ? `/channel/${snippet?.channelId}` : demoChannelUrl}>
          <Typography variant='subtitle2' fontWeight='bold' color='grey'>
            {(snippet?.channelTitle && he.decode(snippet?.channelTitle)) || demoChannelTitle}
            <CheckCircle sx={{ fontSize: 12, color: 'gray', m1: '5px', }}/>
          </Typography>
        </Link>
      </CardContent>
    </Card>
  )
}

export default VideoCard
